body {
	background: #FFF;
	display: none;
	letter-spacing: 0.2pt;
	-webkit-font-smoothing: antialiased;
	@include reg;
	font-size: 17px;
	color: #5b7c96;
	line-height: 1.5;
	display: block;
	display: block;
	display: block;
	display: block; }
img {
	max-width: 100%;
	display: block; }
.section {
	&_margin {
		margin: 100px 0;
		@include r(750) {
			margin: 50px 0; } }
	&__in {
		@include in;
		@include clr;
		display: block; }
	&__pic {
		float: right;
		width: 35%;
		@include r(750) {
			float: none;
			width: auto; } }
	&_half {
		margin-bottom: 100px;
		position: relative;
		z-index: 20;
		@include r(750) {
			margin-bottom: 50px; } }
	&_plat &__pic {
		padding-left: 50px;
		@include r(700) {
			margin-bottom: 80px;
			padding-left: 0; } }
	&_half &__text {
		float: left;
		width: 50%;
		padding-right: 30px;
		@include r(750) {
			float: none;
			width: auto;
			padding-right: 0; } }
	&_wide &__text {
		float: none;
		width: auto;
		padding: 0; }
	&__text {
		float: left;
		width: 64%;
		figure {
			margin-bottom: 50px;
			margin-top: 50px; }
		p {
			margin-bottom: 1em; }
		@include r(750) {
			float: none;
			width: auto; } }
	&_half &__pic {
		float: right;
		width: 50%;
		margin-top: -90px;
		@include r(750) {
			float: none;
			width: auto;
			padding-right: 0;
			margin-top: 20px; } }
	&__short {
		p {
			// max-width: 90%
			@include r(850) {
				max-width: none; } }
		& + .section__pic {
			position: absolute;
			right: 0;
			width: 60%;
			@include r(750) {
				display: none; } } }
	&_ether {
		margin-bottom: 10px;
		.section__in {
			position: relative; } }
	&__more {
		text-align: center; } }
.ipad {
	border: 1px solid #C0CFDC;
	border-radius: 20px;
	height: 0;
	padding-bottom: 140%;
	position: relative;
	visibility: hidden;
	display: none;
	&:before {
		position: absolute;
		content: "";
		top: 16px;
		left: 50%;
		transform: translate(-50%,0);
		width: 5px;
		height: 5px;
		border-radius: 50%;
		border: 1px solid #444; }
	&:after {
		position: absolute;
		content: "";
		width: 16px;
		height: 16px;
		border-radius: 16px;
		border: 1px solid #C0CFDC;
		bottom: 8px;
		left: 50%;
		transform: translate(-50%,0); }
	&__video {
		position: absolute;
		background: #ecf0f3;
		bottom: 36px;
		top: 36px;
		left: 18px;
		right: 18px;
		border: 1px solid #C0CFDC; } }
.more {
	@include semi;
	color: #407dc9;
	text-decoration: none;
	.icon {
		font-size: 14px;
		fill: #407dc9;
		color: #407dc9;
		display: inline-block;
		vertical-align: middle;
		position: relative;
		top: -1px;
		margin-left: 3px; } }
.h2 {
	letter-spacing: 2px;
	text-transform: uppercase;
	font-weight: normal;
	@include semi;
	color: #407dc9;
	font-size: 22px;
	line-height: 1.5;
	margin-bottom: 37px; }
.text {
	p {
		margin-bottom: 1em; } }
.button {
	height: 40px;
	line-height: 40px;
	font-size: 16px;
	@include semi;
	vertical-align: top;
	display: inline-block;
	text-decoration: none;
	border-radius: 5px;
	padding: 0 27px;
	box-shadow: 0 2px 4px rgba(#324555, 0.5);
	color: #fff;
	min-width: 212px;
	text-align: center;
	transition: all .1s linear;
	&:hover {
		box-shadow: 0 2px 5px rgba(#000,.3);
		transform: translate(0,-3px); }
	&:active {
		transform: translate(0,0); }
	.icon {
		color: #fff;
		fill: #fff;
		vertical-align: middle;
		margin-right: 7px; }
	&_gradient {
		background: linear-gradient(to right, #57abef 0%,#2d71c4 100%);
		color: #fff;
		&:hover {
			// background: #2e72c5
			background: linear-gradient(to right, #2e72c5 0%,#2e72c5 100%); } }
	&_subtlebg {
		background: rgba(#fff,0.1);
		box-shadow: none;
		padding-left: 40px;
		padding-right: 40px;
		&:hover {
			background: #fff;
			color: #407dc9; } }
	&_white {
		background: #fff;
		color: #407dc9;
		&:hover {
			background: rgba(#fff,.9);
			color: #407dc9;
			background: #dfe9f5;
			color: #407dc9; }
		&__homepage {
			@include rmin(491) {
				margin-top: 10px !important; } } }
	&_border {
		border: 2px solid #fff;
		line-height: 36px;
		&:hover {
			background: #dfe9f5;
			color: #407dc9;
			border: 2px solid #dfe9f5; } }
	&_spaced {
		padding-left: 40px;
		padding-right: 40px; }
	&_megaspaced {
		padding-left: 70px;
		padding-right: 70px; }
	&_dark {
		border-color: #c0cfdc;
		color: #697f92;
		box-shadow: none;
		&:hover {
			border-color: #5f768a;
			color: #5f768a; }
		.icon {
			color: #7c92a3;
			fill: #7c92a3; } }
	&_mail {
		background: #fff;
		background: #407dc9;
		color: #fff;
		.icon {
			font-size: 13px;
			position: relative;
			display: none;
			top: -2px;
			vertical-align: middle; } } }
.mail-button {
	height: 40px;
	line-height: 40px;
	background: #407dc9;
	color: #fff;
	@include semi;
	font-size: 16px;
	text-align: center;
	padding: 0 46px;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	position: relative;
	left: -4px;
	@include r(500) {
		width: 100%;
		display: block;
		border-radius: 5px;
		left: 0;
		margin-top: 5px; } }
.mail-input {
	border: 2px solid #407dc9;
	border-radius: 5px;
	font-size: 16px;
	color: #407dc9;
	@include semi;
	color: #407dc9;
	padding: 0 24px;
	height: 40px;
	line-height: normal;
	border-top-right-radius: 0;
	min-width: 430px;
	border-bottom-right-radius:  0;
	@include r(720) {
		width: 200px;
		min-width: 200px; }
	@include r(500) {
		width: 100%;
		display: block;
		border-radius: 5px;
		text-align: center; }
	&::placeholder {
		color: #407dc9; } }
.mail-form {
	font-size: 0; }
.bb-select {
	position: relative;
	@include reg;
	font-weight: 600;
	font-size: 17px;
	&.is-open {
		.bb-select__list {
			display: block; }
		.icon-arrowdown {
			transform: rotate(270deg); } }
	.icon-arrowdown {
		fill: #fff;
		transform: rotate(90deg);
		position: absolute;
		right: 0;
		bottom: 4px;
		@include trans; }
	&__val {
		color: #fff;
		cursor: pointer;
		position: relative;
		padding-right: 25px;
		min-width: 100px; }
	&__list {
		display: none;
		position: absolute;
		top: calc(100% + 5px);
		left: 0;
		width: 100%;
		background-color: #fff;
		border-radius: 5px;
		box-shadow: 0 0 10px rgba(0,0,0,.1);
		border: 1px solid #c0cfdc;
		li {
			padding: 5px 25px 5px 15px;
			a {
				color: #407dc9;
				&:hover {
					text-decoration: none;
					opacity: .7; } } } } }
.token-bar {
	min-height: 40px;
	padding: 10px 0;
	background-color: #1669CB;
	box-shadow: 0 0 10px rgba(0,0,0,.1);
	display: flex;
	text-align: center;
	@include r(980) {
		background-color: #fff;
		margin-top: 40px;
		a {
			color: #407dc9; } }
	a {
		max-width: 80%;
		margin: auto;
		font-size: 15px;
		color: #fff;
		text-decoration: underline; } }
