.top-section {
	position: relative;
	z-index: 20;
	margin-top: 150px;
	@include r(600) {
		margin-top: 80px; } }
.thebridge {
	margin-bottom: 100px;
	padding-top: 100px;
	p {
		padding-right: 30px; }
	@include r(750) {
		margin-bottom: 50px;
		margin-top: 50px;
		p {
			padding-right: 0; } } }
.uses {
	@include clr;
	margin-bottom: 100px;
	position: relative;
	z-index: 500;
	display: flex;
	@include r(850) {
		margin-bottom: 50px;
		display: block; } }
.uses-section {
	position: relative; }
.use-popup {
	position: absolute;
	left: 0;
	top: 5px;
	right: 0;
	box-shadow: 0 4px 12px rgba(#6A7F91,0.39);
	border-radius: 5px;
	background: #fff;
	padding: 50px 28px 28px 28px;
	z-index: 600;
	display: none;
	&__less {
		cursor: pointer;
		display: inline-block;
		&:hover {
			color: #000;
			.icon {
				fill: #000; } }
		.icon {
			font-size: 15px;
			transform: rotate(180deg);
			color: #697f92;
			fill: #697f92;
			margin-right: 5px;
			position: relative;
			top: 2px; } }
	&__text {
		font-size: 16px;
		line-height: 1.6;
		p {
			margin-bottom: 1em; } }
	&__title {
		font-size: 17px;
		@include semi;
		letter-spacing: 1.5px;
		color: #407dc9;
		text-transform: uppercase;
		margin: 20px 0; }
	&__close {
		position: absolute;
		right: 20px;
		top: 20px;
		font-size: 21px;
		cursor: pointer;
		&:hover {
			.icon {
				fill: #000; } }
		.icon {
			color: #697f92;
			fill: #697f92; } } }
.use {
	border-radius: 5px;
	border: 1px solid #c0cfdc;
	padding: 54px 28px 28px 28px;
	box-shadow: 0 0 10px rgba(#000,.1);
	min-height: 367px;
	background: #ffffff;
	display: inline-block;
	text-decoration: none;
	color: #5b7c96;
	transition: transform .2s ease-in-out,box-shadow .2s ease-in-out;
	@include three;
	cursor: pointer;
	@include hover {
		transform: translate(0,-5px);
		box-shadow: 0 5px 20px rgba(#000,.3); }
	@include r(850) {
		min-height: auto;
		padding-top: 20px; }
	&:nth-child(3n) {
		margin-right: 0; }
	&__icon {
		margin-bottom: 25px;
		@include r(850) {
			float: right;
			margin-left: 10px; } }
	&__title {
		font-size: 17px;
		margin-bottom: 14px;
		letter-spacing: 1px;
		font-weight: normal;
		@include semi;
		color: #407dc9;
		text-transform: uppercase; } }
.angled-section {
	overflow: hidden;
	position: relative;
	padding-bottom: 97px;
	// margin-bottom: 50px
	@include r(750) {
		padding-bottom: 30px; }
	&>* {
		position: relative;
		z-index: 3; }
	&__angle {
		background: linear-gradient(to right, #52a5eb 0%,#3176c8 100%);
		transform: skewY(-12deg);
		position: absolute;
		left: 0;
		right: 0;
		top: 300px;
		height: 100%;

		&:before {
			content: "";
			top: -35px;
			right: 0;
			left: 65%;
			background: #f0f5fb;
			height: 35px;
			position: absolute; }
		&:after {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			right: 35%;
			height: 80px;
			background: rgba(#fff,0.09); } }
	&_invert {
		padding-bottom: 200px;
		.angled-section__angle {
			background: linear-gradient(to right, #bcc7d0 0%,#9dabb8 100%);
			top: 500px;
			top: 439px;
			@include r(750) {
				top: 300px; }
			@include r(600) {
				top: 400px; }
			@include r(350) {
				top: 477px; }
			&:before {
				top: 0;
				background: rgba(#fff,0.1); }
			&:after {
				top: -35px;
				height: 35px;
				background: #f3f6fa; } } }
	&_invert1 {
		padding-bottom: 100px;
		@include r(750) {
			padding-top: 250px;
			margin-top: -250px; }
		.angled-section__angle {
			background: linear-gradient(to right, #9DABB7 0%,#6A7F91 100%);
			top: 200px;
			@include r(750) {
				top: 200px;
				// transform: skewY(0)
 }				// padding-top: 200px
			@include r(600) {
 }				// top: 0px
			&:before {
				top: 0;
				background: rgba(#fff,0.1); }
			&:after {
				top: -35px;
				height: 35px;
				background: #f3f6fa; } } } }
.revolution {
	color: #fff;
	.h2 {
		color: #fff; } }
.revs {
	@include clr;
	margin-bottom: 70px; }
.rev,.why {
	@include three;
	//padding-left: 44px
	font-size: 17px;
	color: #fff;
	position: relative;
	&:nth-child(n+4) {
		margin-top: 60px;
		@include r(800) {
			margin-top: 0; } }
	&__icon {
		width: 75px;
		height: 75px;
		//border-radius: 30px
		//background: #fff
		//position: absolute
		position: relative;
		left: 0;
		top: -3px;
		.icon {
			color: #407dc9;
			fill: #407dc9;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%,-50%); } }
	&__title {
		font-size: 17px;
		font-weight: normal;
		margin-bottom: 12px;
		@include semi;
		letter-spacing: 1.5px; } }
