.token-overview {
	position: relative;
	z-index: 20;
	margin-top: 180px;
	.section__text {
		overflow: hidden; }
	&__pic {
		float: left;
		width: 155px;
		height: 155px;
		border-radius: 50%;
		background: url(../img/coin2.png) no-repeat 0 0;
		//box-shadow: 0 9px 15px rgba(#000,0.3)
		background-size: cover;
		margin-right: 80px;
		@include r(750) {
			display: none; } }
	@include r(750) {
		margin-top: 100px; } }
.notification {
	position: relative;
	z-index: 20;
	margin: 130px 0 190px;
	.email {
		border: 2px solid #c0cfdc;
		color: #5b7c96;
		border-radius: 5px;
		padding: 8px;
		width: 50%;
		margin-bottom: 5px;
		@include r(700) {
			width: 100%; } }
	@include r(700) {
		margin: 50px 0; }
	&__bg {
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		left: 0;
		z-index: 10;
		transform: skewY(-12deg);
		background: #f6f9fc; }
	&__in {
		position: relative;
		z-index: 20;
		@include in;
		box-shadow: 0 4px 12px rgba(#000,.2);
		border-radius: 5px;
		text-align: center;
		background: #fff;
		padding: 45px 20px;
		@include r(750) {
			padding: 20px; }
		.h2 {
			margin-bottom: 20px; }
		.button {
			margin: 0 10px;
			@include r(600) {
				display: block;
				margin: 10px  0; } }
		p {
			margin-bottom: 13px; } } }
.q {
	margin-bottom: 20px;
	&__title {
		border-radius: 5px;
		border: 2px solid #C0CFDC;
		padding: 12px 40px 12px 20px;
		line-height: 1.2;
		cursor: pointer;
		position: relative;
		transition: all .1s linear;
		&:hover {
			border: 2px solid rgba(#5f768a, .8);
			//background: #f6f9fc
			transition: all .1s linear; }

		.icon {
			position: absolute;
			top: 50%;
			right: 17px;
			color: #407dc9;
			fill: #407dc9;
			transform: translate(0,-50%) rotate(90deg);
			font-size: 20px;
			transition: transform .2s ease-in-out; }
		h3 {
			font-weight: normal;
			@include semi;
			letter-spacing: 1.5px;
			color: #407dc9;
			text-transform: uppercase;
			font-size: 17px; } }
	&__answer {
		background: #f8fafb;
		padding: 20px 25px;
		display: none; }
	&.is-open &__answer {
 }		// display: block
	&.is-open .icon {
		transform: translate(0,-50%) rotate(-90deg); } }
.countdown {
	position: relative;
	z-index: 100;
	.etTitle, .etSep {
		display: none; }
	.eTimer {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	.etUnit {
		width: calc(25% - 49px);
		font-size: 0;
		@include r(880) {
			width: calc(25% - 25px); }
		@include r(650) {
			width: calc(50% - 49px);
			text-align: center;
			margin-bottom: 20px; }
		@include r(425) {
			width: calc(50% - 25px); }
		@include r(360) {
			width: calc(100% - 100px);
			margin-left: auto;
			margin-right: auto; } }

	.etSign {
		margin-top: 22px;
		text-transform: uppercase;
		font-size: 16px;
		letter-spacing: 0.2px; }
	.etNumber {
		position: relative;
		width: calc(50% - 7.5px);
		font-weight: 300;
		font-size: 72px;
		padding: 15px 0;
		@include r(1090) {
			font-size: 48px;
			padding: 20px 0; }
		@include r(880) {
			font-size: 36px;
			padding: 20px 0; }
		&:after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			border-radius: 5px;
			background: linear-gradient(rgba(255,255,255, .4) 0%, rgba(255,255,255,.1) 75%, rgba(255,255,255,.0)); } }

