.header {
	position: absolute;
	top: 40px;
	left: 0;
	right: 0;
	z-index: 1000;
	padding-bottom: 25px;
	&_token {
		top: 0;
		@include r(980) {
			top: 0; } }
	.lang-switcher {
		display: none;
		padding-top: 30px;
		margin-left: 50px;
		float: right;
		text-align: right; }
	@include r(980) {
		padding-bottom: 10px;
		background: #407dc9;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		// &.is-menu
		.lang-switcher {
			padding-top: 9px;
			margin-left: 12px; } }


	@include rmin(981) {
		&.is-fixed {
			background: rgba(#4A6275,.90);
			position: fixed;
			top: 0;
			transform: translate(0,-100%);
			padding-bottom: 15px;
			border-bottom: 2px solid #4A6275;
			&.is-animated {
				transform: translate(0,0);
				transition: transform .2s ease-in-out; }
			&.is-animated-out {
				transform: translate(0,-100%);
				transition: transform .2s ease-in-out; }
			.header__logo {
				padding-top: 17px; }
			.nav {
				a {
					padding-top: 17px; } }
			.lang-switcher {
				padding-top: 17px; }
			.whitepaper-switcher {
				padding-top: 17px;
				.bb-select__list a {
					padding-top: 0; } } } }
	&__burger,&__close {
		display: none; }
	&__close {
		@include r(980) {
			.is-menu & {
				cursor: pointer;
				display: block;
				.icon {
					fill:#fff {}
					position: absolute;
					font-size: 26px;
					right: 10px;
					top: 10px;
					z-index: 10; } } } }
	&__burger {
		@include r(980) {
			display: block;
			position: absolute;
			font-size: 20px;
			right: 130px;
			top: 10px;
			z-index: 10;
			.is-menu & {
				display: none; }
			.icon {
				fill: #fff; } } }

	&__in {
		@include in;
		display: block; }
	&__logo {
		float: left;
		padding-top: 30px;
		width: 164px;
		@include hide-text;
		@include r(980) {
			width: 150px;
			padding-top: 10px;
			z-index: 10;
			position: relative; } } }
.nav {
	@include semi;
	font-size: 17px;
	float: right;
	@include r(980) {
		display: none;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: #407dc9;
		z-index: 1;
		padding-top: 50px;
		padding-left: 20px;
		.is-menu & {
			display: block; }
		li,a {
			float: none;
			padding: 0;
			margin: 0;
			@include reg;
			font-size: 22px;
			text-align: center;
			&:before {
				display: none; } }
		a {
			display: block;
			padding: 10px 0;
			&.is-active {
				text-decoration: underline; } } }

	li {
		float: left; }
	a {
		text-decoration: none;
		float: left;
		color: #fff;
		margin-left: 12px;
		padding: 30px 5px  0 5px;
		position: relative;
		&.is-active {
			&:before {
				height: 3px;
				background: #fff;
				top: 0;
				left: 0;
				right: 0;
				position: absolute;
				content: ""; } } } }
.whitepaper-switcher {
	padding: 30px 5px 0 12px;
	text-align: left;
	@include r(980) {
		padding: 10px 0;
		font-weight: 400;
		font-size: 22px;
		display: inline-block;
		.bb-select__list {
			width: 120%;
			left: -10%;
			li a {
				text-align: left;
				padding: 10px 10%; } } }


	.bb-select__list {
		a {
			padding: 0;
			margin: 0; } } }
