.screens-wrap {
	@include in;
	@include r(750) {
		display: none; } }
.why-invest {
	padding-top: 100px;
	@include r(750) {
		padding-top: 50px; }
	.h2 {
		color: #fff; } }
body .why {
	&__icon {
		.icon {
			fill: #697f92;
			color: #697f92; } }
	&:nth-child(n+4) {
		margin-top: 60px;
		@include r(800) {
			margin-top: 0; } } }
.howwe {
	padding-top: 130px;
	padding-bottom: 220px;
	background: #f6f9fc;
	.section__in {
		font-size: 0; }
	@include r(750) {
		padding-bottom: 50px;
		padding-top: 50px;
		margin-bottom: 50px; }
	&__pic {
		float: right;
		width: 50%;
		position: relative;
		top: -100px;

		display: inline-block;
		float: none;
		vertical-align: bottom;
		position: absolute;
		bottom: 30px;
		right: 0;
		top: auto;

		@include r(750) {
			// display: none
			float: none;
			width: auto;
			top: 0;
			padding-bottom: 40px;
			position: static; } }
	&__list {
		display: inline-block;
		float: none;
		vertical-align: bottom;
		width: 50%;
		padding-right: 5%;
		font-size: initial;
		@include r(750) {
			width: auto;
			padding-right: 0; }
		.how {
			width: auto;
			max-width: none; } }
	&__three {
		font-size: initial;
		.how {
			@include three; } }
	&__wrap {
		position: relative; } }
.how {
	display: inline-block;
	vertical-align: top;
	padding-left: 44px;
	position: relative;
	max-width: 33%;
	margin-bottom: 64px;
	@include r(750) {
		max-width: none;
		margin-bottom: 20px; }
	&__icon {
		width: 24px;
		height: 28px;
		background: url(../img/shield.png) no-repeat 0 0;
		background-size: contain;
		position: absolute;
		left: 0;
		.icon-check {
			color: #fff;
			fill: #fff;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%); } }
	&__title {
		font-size: 17px;
		font-weight: normal;
		margin-bottom: 12px;
		@include semi;
		color: #407dc9;
		letter-spacing: 1.5px; } }
