/* Main */
.flip-clock-wrapper { font: normal 11px "Helvetica Neue", Helvetica, sans-serif; -webkit-user-select: none; }

.flip-clock-meridium { background: none !important; box-shadow: 0 0 0 !important; font-size: 36px !important; }

.flip-clock-meridium a { color: #313333; }

.flip-clock-wrapper { text-align: center; position: relative; width: 100%; /*margin: 1em;*/ }

.flip-clock-wrapper:before, .flip-clock-wrapper:after { content: " "; /* 1 */ display: table; /* 2 */ }

.flip-clock-wrapper:after { clear: both; }

/* Skeleton */
.flip-clock-wrapper ul { position: relative; float: left; margin: 5px; width: calc(12% - 20px); font-size: 80px; font-weight: 100; letter-spacing: 1.27px; border-radius: 5px; overflow: hidden; }

.flip-clock-wrapper ul li { z-index: 1; position: absolute; left: 0; top: 0; width: 100%; height: 100%; text-decoration: none !important; }

.flip-clock-wrapper ul li:first-child { z-index: 2; }

.flip-clock-wrapper ul li a { display: block; height: 100%; -webkit-perspective: 200px; -moz-perspective: 200px; perspective: 200px; margin: 0 !important; overflow: visible !important; cursor: default !important; }

.flip-clock-wrapper ul li a div { z-index: 1; position: absolute; left: 0; width: 100%; height: 50%; font-size: 80px; overflow: hidden; outline: 1px solid transparent; }

.flip-clock-wrapper ul li a div .shadow { position: absolute; width: 100%; height: 100%; z-index: 2; }

.flip-clock-wrapper ul li a div.up { -webkit-transform-origin: 50% 100%; -moz-transform-origin: 50% 100%; -ms-transform-origin: 50% 100%; -o-transform-origin: 50% 100%; transform-origin: 50% 100%; top: 0; }

.flip-clock-wrapper ul li a div.up:after { content: ""; display: none; position: absolute; top: 44px; left: 0; z-index: 5; width: 100%; height: 3px; background-color: #000; background-color: rgba(0, 0, 0, 0.4); }

.flip-clock-wrapper ul li a div.down { -webkit-transform-origin: 50% 0; -moz-transform-origin: 50% 0; -ms-transform-origin: 50% 0; -o-transform-origin: 50% 0; transform-origin: 50% 0; bottom: 0; border-bottom-left-radius: 6px; border-bottom-right-radius: 6px; }

.flip-clock-wrapper ul li a div div.inn { position: absolute; left: 0; z-index: 1; width: 100%; height: 200%; color: #377bc7; /*text-shadow: 0 1px 2px #000;*/ text-align: center; background-color: #fff; border-radius: 5px; font-size: 72px; }

.flip-clock-wrapper ul li a div.up div.inn { top: 0; }

.flip-clock-wrapper ul li a div.down div.inn { bottom: 0; }

/* PLAY */
.flip-clock-wrapper ul.play li.flip-clock-before { z-index: 3; }

.flip-clock-wrapper .flip { box-shadow: 0 4px 12px rgba(106, 127, 145, 0.39); }

.flip-clock-wrapper ul.play li.flip-clock-active { -webkit-animation: asd 0.5s 0.5s linear both; -moz-animation: asd 0.5s 0.5s linear both; animation: asd 0.5s 0.5s linear both; z-index: 5; }

.flip-clock-divider { float: left; display: inline-block; position: relative; width: 20px; height: 100px; }

.flip-clock-divider:first-child { width: 0; }

.flip-clock-dot { display: none; background: #323434; width: 10px; height: 10px; position: absolute; border-radius: 50%; box-shadow: 0 0 5px rgba(0, 0, 0, 0.5); left: 5px; }

.flip-clock-divider .flip-clock-label { font-size: 16px; position: absolute; bottom: -4em; /*right: -140px;*/ color: #5c7c95; letter-spacing: 0.2px; text-transform: uppercase; text-align: center; text-shadow: none; }

/*.flip-clock-divider.minutes .flip-clock-label { right: -148px; } .flip-clock-divider.minutes .flip-clock-label { right: -155px; } .flip-clock-divider.seconds .flip-clock-label { right: -155px; }*/
.flip-clock-dot.top { top: 30px; }

.flip-clock-dot.bottom { bottom: 30px; }

@-webkit-keyframes asd { 0% { z-index: 2; }
  20% { z-index: 4; }
  100% { z-index: 4; } }

@-moz-keyframes asd { 0% { z-index: 2; }
  20% { z-index: 4; }
  100% { z-index: 4; } }

@-o-keyframes asd { 0% { z-index: 2; }
  20% { z-index: 4; }
  100% { z-index: 4; } }

@keyframes asd { 0% { z-index: 2; }
  20% { z-index: 4; }
  100% { z-index: 4; } }

.flip-clock-wrapper ul.play li.flip-clock-active .down { z-index: 2; -webkit-animation: turn 0.5s 0.5s linear both; -moz-animation: turn 0.5s 0.5s linear both; animation: turn 0.5s 0.5s linear both; }

@-webkit-keyframes turn { 0% { -webkit-transform: rotateX(90deg); }
  100% { -webkit-transform: rotateX(0deg); } }

@-moz-keyframes turn { 0% { -moz-transform: rotateX(90deg); }
  100% { -moz-transform: rotateX(0deg); } }

@-o-keyframes turn { 0% { -o-transform: rotateX(90deg); }
  100% { -o-transform: rotateX(0deg); } }

@keyframes turn { 0% { transform: rotateX(90deg); }
  100% { transform: rotateX(0deg); } }

.flip-clock-wrapper ul.play li.flip-clock-before .up { z-index: 2; -webkit-animation: turn2 0.5s linear both; -moz-animation: turn2 0.5s linear both; animation: turn2 0.5s linear both; }

@-webkit-keyframes turn2 { 0% { -webkit-transform: rotateX(0deg); }
  100% { -webkit-transform: rotateX(-90deg); } }

@-moz-keyframes turn2 { 0% { -moz-transform: rotateX(0deg); }
  100% { -moz-transform: rotateX(-90deg); } }

@-o-keyframes turn2 { 0% { -o-transform: rotateX(0deg); }
  100% { -o-transform: rotateX(-90deg); } }

@keyframes turn2 { 0% { transform: rotateX(0deg); }
  100% { transform: rotateX(-90deg); } }

.flip-clock-wrapper ul li.flip-clock-active { z-index: 3; }

/* SHADOW */
.flip-clock-wrapper ul.play li.flip-clock-before .up .shadow { background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%); background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 0.1)), color-stop(100%, rgba(106, 127, 145, 0.39))); background: linear, top, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%; background: -o-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%); background: -ms-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%); background: linear, to bottom, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%; -webkit-animation: show 0.5s linear both; -moz-animation: show 0.5s linear both; animation: show 0.5s linear both; }

.flip-clock-wrapper ul.play li.flip-clock-active .up .shadow { background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%); background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 0.1)), color-stop(100%, rgba(106, 127, 145, 0.39))); background: linear, top, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%; background: -o-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%); background: -ms-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%); background: linear, to bottom, rgba(0, 0, 0, 0.1) 0%, rgba(106, 127, 145, 0.39) 100%; -webkit-animation: hide 0.5s 0.3s linear both; -moz-animation: hide 0.5s 0.3s linear both; animation: hide 0.5s 0.3s linear both; }

/*DOWN*/
.flip-clock-wrapper ul.play li.flip-clock-before .down .shadow { background: -moz-linear-gradient(top, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%); background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(106, 127, 145, 0.39)), color-stop(100%, rgba(0, 0, 0, 0.1))); background: linear, top, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%; background: -o-linear-gradient(top, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%); background: -ms-linear-gradient(top, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%); background: linear, to bottom, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%; -webkit-animation: show 0.5s linear both; -moz-animation: show 0.5s linear both; animation: show 0.5s linear both; }

.flip-clock-wrapper ul.play li.flip-clock-active .down .shadow { background: -moz-linear-gradient(top, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%); background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(106, 127, 145, 0.39)), color-stop(100%, rgba(0, 0, 0, 0.1))); background: linear, top, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%; background: -o-linear-gradient(top, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%); background: -ms-linear-gradient(top, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%); background: linear, to bottom, rgba(106, 127, 145, 0.39) 0%, rgba(0, 0, 0, 0.1) 100%; -webkit-animation: hide 0.5s 0.3s linear both; -moz-animation: hide 0.5s 0.3s linear both; animation: hide 0.5s 0.2s linear both; }

