.pie-chart svg .el { transform-origin: 0% 100%; transition: all 0.2s ease; }

.pie-chart svg .el:hover, .pie-chart svg .el.is-animated { transform: scale(1.1); }

.pie-chart svg .el[data-color="dark-blue"]:hover, .pie-chart svg .el[data-color="dark-blue"].is-animated { transform: translate(-15px, 5px) scale(1.05); }

.col-chart svg .el { transform-origin: 50% 100%; transition: all 0.2s ease; }

.col-chart svg .el:hover, .col-chart svg .el.is-animated { transform: scaleY(1.15); }

.col-chart svg .el[data-color="dark-blue"]:hover, .col-chart svg .el[data-color="dark-blue"].is-animated { transform: scaleY(1.05); }

.area-chart { display: none; margin-bottom: 20px; }

.area-chart.is-active { display: block; }

.legend { display: flex; justify-content: space-between; flex-wrap: wrap; }

.legend__item { width: 45%; padding-left: 22px; position: relative; font-size: 15px; letter-spacing: 0.26px; margin-bottom: 10px; cursor: pointer; }

.legend__item:hover, .legend__item.is-bold { font-weight: 900; }

.legend__item:before { content: ""; display: block; position: absolute; top: 6px; left: 0; width: 10px; height: 10px; border-radius: 50%; }

.legend__item_wide { width: 100%; }

.legend__item_dark-blue:before { background-color: #398bdf; }

.legend__item_middle-blue:before { background-color: #00acee; }

.legend__item_light-blue:before { background-color: #00d7e6; }

.legend__item_purple:before { background-color: #c668e7; }
