$space-x: 38px;
$space-y: 24px;
$icon-size: 74px;

.media {
	max-width: 1050px;
	margin: 0 auto;
	position: relative;
	top: -50px;
	z-index: 3;
	@include r(1090) {
		margin: 0 20px; }
	&__badge {
		margin-bottom: 23px; } }
.badge {
	position: relative;
	box-shadow: 0 4px 12px rgba(106, 127, 145, 0.39);
	background-color: #fff;
	border-radius: 5px;
	transition: transform .2s ease-in-out,box-shadow .2s ease-in-out;
	@include hover {
		transform: translate(0,-5px);
		box-shadow: 0 5px 20px rgba(#000,.3); }
	&__in {
		display: block;
		padding: $space-y $space-x;
		@include clr; }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		width: $icon-size;
		height: $icon-size;
		border-radius: 50%;
		position: relative;
		&_post {
			background: linear-gradient(165deg, #da73f6 0%, #b860de 100%); }
		&_news {
			background: linear-gradient(-52deg, #3173c1 0%, #5caeee 100%); }
		&_video {
			background: linear-gradient(265deg, #00b2f6 0%, #00a7e7 100%); }
		&_url {
			background: linear-gradient(51deg, #00e5f5 0%, #00ccdb 100%); }
		@include r(500) {
			display: block;
			margin: 0 auto $space-x / 2; }
		.icon {
			@include vcenter; }
		.icon-news {
			width: 24px;
			height: 24px; }
		.icon-post {
			width: 18px;
			height: 23px; }
		.icon-video {
			width: 20px;
			height: 16px; }
		.icon-url {
			width: 28px;
			height: 28px; } }
	&__content {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 74px - 20px);
		padding-left: $space-x;
		text-align: left;
		@include r(500) {
			width: 100%;
			padding-left: 0;
			text-align: center; } }

	&__title {
		text-transform: uppercase;
		@include semi;
		letter-spacing: 1.55px;
		font-size: 17px;
		color: #437fc6;
		text-decoration: none;
		&:active, &:hover {
			text-decoration: none; }
		&:hover {
			opacity: .8; }
		@include r(500) {
			margin-bottom: $space-x / 2; } }
	&__meta {
		@include r(500) {
			margin: 0 auto; }
		& > span {
			font-size: 17px;
			letter-spacing: 0.4px;
			line-height: 26px;
			color: #5c7c95;
			display: table-cell;
			vertical-align: middle;
			@include r(370) {
				display: block;
				padding: 0;
				margin: 0 auto; } } }

	&__source {
		@include semi;
		min-width: 155px;
		max-width: 155px;
		padding-right: 30px;
		word-wrap: break-word; }

	&__date {
		@include reg;
		&-icon, &-text {
			display: inline-block;
			vertical-align: middle; }
		&-icon {
			opacity: .4;
			margin-right: 11px;
			svg {
				display: block; } } } }
