.community {
	position: relative;
	z-index: 10;
	&__in {
		@include in;
		padding: 80px 0 90px;
		@include r(750) {
			padding: 50px 0 50px; } }
	&__icons {
		display: flex;
		flex-wrap: nowrap;
		flex-direction: row;
		justify-content: space-between;
		font-size: 0;
		@include r(660) {
			display: block;
			li {
				display: inline-block;
				margin: 2px; } }
		a {
			width: 65px;
			height: 65px;
			background: #6095d1;
			border-radius: 50%;
			display: inline-block;
			position: relative;
			will-change: transform;
			transition: transform .2s ease-in-out;
			@include r(660) {
				display: inline-block;
				vertical-align: top; }
			@include hover {
				transform: scale(1.1);
				span {
					opacity: 1;
					transform: translate(0,0); } }
			span {
				color: #2D3D4B;
				position: absolute;
				top: 100%;
				text-align: center;
				padding-top: 5px;
				right: 0;
				left: 0;
				opacity: 0;
				font-size: 12px;
				will-change: transform;
				transform: translate(0,-10px);
				transition: transform .2s ease-in-out,opacity .2s ease-in-out;
				@include r(750) {
					display: none; } }
			.icon {
				color: #fff;
				fill: #fff;
				font-size: 30px;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%); } } } }
body {
	padding-bottom: 370px;
	@include r(860) {
		padding-bottom: 0; }
	.out {
		position: relative;
		z-index: 12;
		background: #fff;
		box-shadow: 0 0 10px rgba(#000,.4);
		@include r(860) {
			box-shadow: none; } } }
.footer {
	background: #4A6275;
	//position: relative
	z-index: 10;
	height: 370px;
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	.lang-switcher {
		display: none;
		text-align: left;
		@include r(860) {
			width: 100px; }
		&__val {
			padding-left: 15px;
			@include r(860) {
				padding: 0;
				min-width: 80px; } } }
	@include r(860) {
		position: relative;
		height: auto; }
	&__in {
		@include in;
		@include clr;
		color: #fff;
		padding: 63px 0 30px;
		@include r(860) {
			padding: 30px 0; } }
	&__logo {
		float: left;
		width: 160px;
		@include r(860) {
			float: none;
			margin-bottom: 20px; } }
	&__cols {
		float: right;
		width: 80%;
		text-align: right;
		margin-bottom: -1.5em;
		font-size: 0;
		@include r(860) {
			margin-bottom: 0;
			float: none;
			width: auto; } }
	&__col {
		display: inline-block;
		vertical-align: top;
		margin-right: 50px;
		text-align: left;
		padding-right: 5px;
		@include r(860) {
			margin: 0;
			display: inline-block;
			width: 50%;
			margin-bottom: 20px; }
		h3 {
			font-weight: normal;
			@include bold;
			font-size: 16px;
			color: #fff;
			margin-bottom: 12px; }

		li {
			margin-bottom: 12px;

			@include r(860) {
				margin-bottom: 4px; } }
		a {
			text-decoration: none;
			color: #fff;
			font-size: 16px;
			@include r(860) {
				font-size: 13px; }
			&:hover {
				text-decoration: underline; } }
		&_short {
			margin-right: 0;
			@include r(975) {
				margin-right: 50 - 5 + px;
				.lang-switcher__list {
					top: auto;
					bottom: 100%;
					li {
						margin-bottom: 0; } } } } }
	&__copy {
		font-size: 16px;
		clear: both;
		@include r(600) {
			font-size: 13px; } } }
