.bg-section {
	background: #f6f9fc;
	margin-bottom: 100px;
	padding-bottom: 60px;
	@include r(750) {
		padding-bottom: 20px;
		margin-bottom: 50px;
		padding-top: 30px; } }
.person {
	@include three;
	margin-bottom: 60px;
	font-size: 16px;
	&__photo {
		width: 140px;
		height: 140px;
		display: inline-block;

		margin-bottom: 18px;
		cursor: pointer;
		position: relative;
		text-decoration: none;
		&-pic {
			border: 3px solid #407dc9;
			background-color: #407dc9;
			background-blend-mode: luminosity;
			background-size: cover;
			border-radius: 50%;
			width: 140px;
			height: 140px;
			opacity: .9; }

		&:hover {
			.person__linkedin {
				transform: scale(1);
				opacity: .88; } } }
	&__linkedin {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(to right, #65abea 0%,#4e8bd1 100%);
		border-radius: 50%;
		transform: scale(0.5);
		opacity: 0;
		transition: opacity .2s ease-in-out,transform .2s ease-in-out;
		will-change: transform;
		.icon {
			font-size: 30px;
			color: #fff;
			fill: #fff;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%); } }
	&__name {
		letter-spacing: 1.5px;
		font-size: 17px;
		text-transform: uppercase;
		color: #407dc9;
		font-weight: normal;
		@include semi;
		margin-bottom: 5px;
		&__link {
			text-decoration: none !important; } }
	&__position {
		font-size: 17px;
		color: #5b7c96;
		margin-bottom: 12px; }
	p {
		padding-right: 20px; } }
