.hero {
	// background: #7ba9dd
	position: relative;
	&__action {
		margin-bottom: 58px;
		margin-top: 33px;
		.button {
			margin-right: 18px;
			margin-bottom: 5px;
			@include r(400) {
				display: block;
				text-align: center;
				margin-bottom: 5px;
				margin-right: 0; } }
		&_spaced {
			padding-bottom: 30px;
			.button {
				padding-left: 50px;
				padding-right: 50px; } } }
	&__content {
		position: relative;
		z-index: 10;
		font-size: 17px;
		color: #fff;
		&.ifstart {
			max-width: 535px; }
		h2 {
			font-size: 42px;
			font-weight: normal;
			margin-bottom: 10px;
			@include semi;
			@include r(600) {
				font-size: 25px; } } }
	&__over {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		background: red;
		z-index: 3;
		&-right {
			position: absolute;
			content: "";
			left: 0;
			right: 0;
			top: 100%;
			transform: skewY(-12deg);
			&:before {
				content: "";
				top: 0;
				right: 0;
				left: 65%;
				background: #f0f5fb;
				height: 35px;
				position: absolute; }

			&:after {
				content: "";
				position: absolute;
				left: 0;
				top: -80px;
				right: 35%;
				height: 80px;
				background: rgba(#fff,0.09); } }

		&:before {
			content: "";
			position: absolute;
			top: 100%;
			width: 100%;
			height: 700px;
			left: 0;
			right: 0;
			background: #fff;
			transform: skewY(-12deg); } }

	&__bg {
		position: absolute;
		background: #5da8fd url(../img/bg2.png) no-repeat 0 0;
		// background-blend-mode: multiply
		background-size: cover;
		top: 0;
		left: 0;
		bottom: -400px;
		right: 0; }
	&__pic {
		position: absolute;
		top: 85px;
		right: -100px;
		width: 550px;
		width: 700px;
		height: 550px;
		background: url(../img/brickblock_laptop_black_dashboard.png) no-repeat 100% 0;
		background-size: contain;
		z-index: 200;
		@include rmin(1300) {
			right: -50px; }
		@include rmin(1530) {
			right: 00px;
			width: 800px;
			height: 650px; }
		@include rmin(1800) {
			right: 120px; }
		@include rmin(2100) {
			right: 400px; }
		@include r(1200) {
			right: -230px; }
		@include r(980) {
			display: none; } }
	&__in {
		@include in;
		padding: 200px 0 50px 0;
		@include r(800) {
			padding-top: 100px; } } }
.video {
	display: inline-block;
	text-decoration: none;
	color: #fff;
	vertical-align: middle;
	visibility: hidden;
	&__icon {
		display: inline-block;
		vertical-align: middle;
		margin-right: 14px;
		width: 44px;
		height: 44px;
		border: 2px solid #fff;
		background: rgba(#fff,0.32);
		position: relative;
		border-radius: 50%;
		.icon {
			font-size: 10px;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%,-50%); } }

	&__text {
		display: inline-block;
		vertical-align: middle; }
	span {
		display: block;
		font-size: 12px;
		@include semi;
		margin-bottom: 2px; }
	strong {
		display: block;
		text-transform: uppercase;
		@include bold; } }
