.press {
	text-align: center;
	position: relative;
	overflow: hidden;
	padding-top: 100px;
	display: none;
	@include r(750) {
		padding-top: 50px; }
	// padding-top: 200px
	// margin-top: -200px
	&:before {
		content: "";
		position: absolute;
		top: 200px;
		left: 0;
		right: 0;
		height: 160%;
		background: #f6f9fc;
		transform: skewY(-12deg); }
	&>* {
		position: relative;
		z-index: 2; }
	&__image {
		text-align: center;
		margin-bottom: 56px;
		@include r(750) {
			display: none; }
		img {
			display: inline-block;
			width: 50%; } } }

.press-logos {
	position: relative;
	padding-bottom: 71px;
	@include r(600) {
		padding-bottom: 30px; }
	&__logos {
		transition: 1s cubic-bezier(.19,1,.22,1);
		will-change: transform;
		display: flex;
		opacity: 1;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: space-between;
		align-items: center;
		@include r(750) {
			flex-wrap: wrap;
			a {
				width: 50%;
				padding: 15px; } } }
	&__buttons {
		text-align: center;
		position: absolute;
		transform: translate(0,5px) scale(.95);
		opacity: 0;
		//opacity: 1
		left: 0;
		right: 0;
		top: 0;
		padding-top: 10px;
		transition: .7s cubic-bezier(.19,1,.22,1);
		@include r(750) {
			position: static;
			opacity: 1;
			text-align: center; }
		.button {
			padding: 0 80px;
			margin: 0 10px;
			@include r(750) {
				margin: 5px; }
			@include r(600) {
				display: block;
				width: 100%;
				text-align: center; } } }
	@include hover {
		.press-logos__buttons {
			opacity: 1;
			transform: translate(0,0) scale(1); }
		.press-logos__logos {
			opacity: .8;
			transform: scale(.95);
			//filter: url(data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg"><f…ter id="filter"><feGaussianBlur stdDeviation="10" /></filter></svg>#filter)
			filter: blur(10px); } } }
