	&__timeslider {
		padding-top: 48px;
		padding-left: 40px;
		&:before {
			background: linear-gradient(-51deg, #5caeee 0%, #3173c1 100%);
			top: 100px; }
		.slick-dots {
			bottom: -70px;
			margin-left: -20px; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		text-align: left;
		cursor: ew-resize;
		position: relative;
		color: #5b7c96;
		&:hover, &.is-special {
			h3, ul {
				opacity: 1; } }
		&.is-link {
			cursor: pointer; }
		&.is-circled {
