		&.is-special {
			.timeslider__index {
				margin-left: 8px;
				span {
					background-color: #459ee8;
					color: #fff;
					width: 32px;
					height: 32px;
					left: 0;
					top: 0; }
				&:after {
					border-color: #459ee8;
					background-color: #fff;
					width: 48px;
					height: 48px;
					top: -8px;
					left: -8px; } } }
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 48px + 4px;
			width: 100%;
			height: 4px;
			background: #ecf0f3; }
		p {
			width: calc(100% - 10px); }
		h3 {
			font-size: 17px;
			@include semi;
			color: #407dc9;
			text-transform: uppercase;
			letter-spacing: 1.5px;
			position: relative;
			z-index: 100;
			margin-bottom: 19px;
			line-height: 1;
			opacity: .5; }
		ul {
			@include reg;
			letter-spacing: 0.2px;
			line-height: 30px;
			font-size: 0;
			opacity: .5;
			max-width: percentage(180/225);
			li:before {
				font-size: 16px;
				content: '-';
				width: 10px;
				display: inline-block;
				vertical-align: top;
				position: absolute;
				left: 0; }
			span {
				font-size: 16px;
				display: inline-block;
				vertical-align: top;
				padding-left: 10px; } } }
	&__bg-title {
		span {
			background-color: #0080e8; } }


