.buttons {
	margin-top: 33px; }
.out {
	overflow: hidden; }

.section_partners {
	margin-top: 250px;
	@include r(750) {
		margin-top: 150px; }

	.h2 {
		color: #fff; } }
.partners {
	display: flex;
	flex-wrap: nowrap;
	flex-direction: row;
	justify-content: space-between;
	@include r(750) {
		flex-wrap: wrap; }
	li {
		@include r(850) {
			width: 50%;
			text-align: center; }
		&:first-child {
			position: relative;
			// left: -15px
			@include r(850) {
				left: 0; } } } }
.home__notification {
 z-index: 10;
 position: relative;
 transform: translateY(-50%);
 @include r(750) {
	 transform: none;
	 margin-bottom: 50px; } }

.contribute {
	border-radius: 5px;
	background: #fff;
	padding: 70px 70px 70px 140px;
	color: #232c34;
	transform: translate(0,-50%);
	position: relative;
	z-index: 20;
	@include in;
	overflow: hidden;
	box-shadow: 0 0 20px rgba(#000,.2);
	@include r(750) {
		transform: none;
		margin-bottom: 50px; }
	@include r(600) {
		padding: 30px 20px;
		text-align: center; }
	&:before {
		width: 145px;
		height: 145px;
		content: "";
		background: #fff  url(../img/coin.png) no-repeat 0 0;
		position: absolute;
		background-size: contain;

		top: -10px;
		left: -12px;
		@include r(600) {
			display: none; } }

	&__content {
		float: left;
		width: 55%;
		position: relative;
		z-index: 2;
		@include r(900) {
			float: none;
			width: auto; } }
	&__more {
		overflow: hidden;
		margin-top: 0;
		text-align: center;
		padding-top: 15px;
		@include r(900) {
			float: none;
			width: auto;
			text-align: left; } }
	.button {
		padding-left: 50px;
		box-shadow: none;
		padding-right: 50px;
		@include r(600) {
			display: block;
			text-align: center; } }

	.h2 {
		margin-bottom: 20px;
		@include r(600) {
			margin-bottom: 5px; } }
	&_simple {
		background: #fff;
		padding-left: 70px;
		transform: none;
		margin: 100px auto 50px auto;
		&:before {
			display: none; } } }

$step: -80px;
.ether {
	.angled-section__angle {
		top: 650px; }
	.section__text {
		float: right; }
	.section__pic {
		float: left;
		max-height: 650px;
		margin-top: -70px;
		img {
			max-width: 364px;
			position: relative;
			&:nth-child(1) {
				z-index: 6;
				top: 0; }
			&:nth-child(2) {
				z-index: 5;
				top: $step; }
			&:nth-child(3) {
				z-index: 4;
				top: $step * 2; }
			&:nth-child(4) {
				z-index: 3;
				top: $step * 3; }
			&:nth-child(5) {
				z-index: 2;
				top: $step * 4; }
			&:nth-child(6) {
				z-index: 1;
				top: $step * 5; } } } }

.changing-title {
	@include trans(opacity, .6s); }
